@import '../../styles/settings/variables';

.formStep {
  background-image: url("../../assets/Background-Form.jpg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #e8e7e5;

  .rowForm {
    flex-direction: column;
    padding: 35px 0 50px;
    align-items: flex-start;

    p {
      font-size: 2.4em;
      line-height: 1.1em;
      font-weight: 300;
      color: $colorText;
      margin-bottom: 70px;
    }

    h3 {
      font-size: 3.6em;
      line-height: 1.25em;
      font-weight: 600;
      margin-bottom: 40px;
    }

    .stepOneLabel {
      position: relative;
      font-size: 2.4em;
      font-weight: 300;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 42px;
      cursor: pointer;

      &:hover {
        .checkmark {
          background-color: $colorBackgroundAlt;
        }
      }

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked {
          ~ .checkmark {
            background-color: $colorBackgroundAlt;
          }
        }
      }

      .checkmark {
        width: 40px;
        height: 40px;
        border: .75px solid $colorText;
        margin-right: 30px;
      }
    }

    .stepTwoLabel {
      margin-top: 10px;
      font-size: 2.4em;
      line-height: 1.33em;
      font-weight: 300;
      display: flex;
      align-items: center;
      margin-bottom: 350px;

      select {
        font-size: 1em;
        font-weight: 300;
        padding: 8px 140px 8px 18px;
        margin-left: 25px;
        border: .75px solid $colorText;
        background-color: transparent;
      }
    }

    .stepThreeText {
      font-size: 2.4em;
      line-height: 1.33em;
      font-weight: 600;
      display: block;
      margin-bottom: 20px;
      color: #000;
    }

    .firstStepThreeLabel {
      margin-top: -20px;

      select {
        font-size: 2.4em;
        line-height: 1.33em;
        font-weight: 300;
        padding: 8px 140px 8px 18px;
        margin-bottom: 50px;
      }

      span {
        font-size: 2.4em;
        line-height: 1.33em;
        font-weight: 600;
        display: block;
        margin-bottom: 20px;
      }
    }

    .checkmarkBox {
      margin-bottom: 25px;
      width: 50%;

      .stepThreeLabel {
        input {
          position: absolute;
          opacity: 0;
          cursor: pointer;
          height: 0;
          width: 0;

          &:checked {
            ~ .checkmark {
              background-color: $colorBackgroundAlt;
            }
          }
        }

        &:hover {
          .checkmark {
            background-color: lighten($colorBackgroundAlt,10%);
          }
        }

        span {
          display: inline-block;
          font-size: 2.4em;
          line-height: 1.33em;
          font-weight: 300;
          padding: 11px 25px;
          border: .75px solid $colorText;
          margin: 0 25px 25px 0;
        }
      }
    }

    .lastStepThreeLabel {
      width: 50%;
      input {
        background-color: transparent;
        border: .75px solid $colorText;
        width: 80%;
        font-size: 2.4em;
        line-height: 1.33em;
        font-weight: 300;
        padding: 11px 25px;
      }
    }

    .btns {
      display: flex;
    }

    .forward {
      font-size: 2.4em;
      line-height: 1.33em;
      font-weight: 300;
      border: .75px solid $colorText;
      padding: 23px 63px;
      margin-top: 118px;
      @include hoverBtn;
    }

    .backward {
      font-size: 2.4em;
      line-height: 1.33em;
      font-weight: 300;
      border: .75px solid $colorText;
      padding: 23px 52px;
      margin: 118px 50px 0 0;
      @include hoverBtn;
    }
  }
}